caption,
.table-header {
  @include clearfix();
  h2,h3 {
    font-weight: bold;
    font-size: 14px;
    line-height: normal;
    margin:0;
    > i {
      padding-right: 10px;
    }
  }
  font-weight: bold;
  font-size: 14px;
  margin-bottom: 0;
  border: 1px solid #ccc;
  border-bottom: none;
  //#gradient > .vertical(#F8F8F8, #e5e5e5);
  color: #242424;
  padding: 6px 10px;
  @include border-radius(3px 3px 0 0);
  text-align: left;
  // line-height: 28px;
  .btn-toolbar {
    margin-top:0;
    margin-bottom:0;
  }
  > i {
    padding-right: 10px;
  }
}



caption {
  caption-side: top;
}
